import CardAchievements from "../CardAchievements/CardAchievements";
import "./OurAchievements.css";
import People from "../../assets/images/People.png"
import InLove from "../../assets/images/InLove.png"
import Confetti from "../../assets/images/Confetti.png"
import Doll from "../../assets/images/Doll.png"


function OurAchievements() {
  return (
    <div className="achievements">
      <CardAchievements img={People} alt=" "> 20-   </CardAchievements>
      <CardAchievements img={InLove} alt=" ">   </CardAchievements>
      <CardAchievements img={Confetti} alt=" ">  10-   </CardAchievements>
      <CardAchievements img={Doll} alt=" ">   ,  </CardAchievements>
    </div>
  )
}

export default OurAchievements;